button,\r\ninput[type='search']::-webkit-search-decoration {\r\n    -webkit-appearance: none;\r\n}\r\n\r\n/**\r\n * Remove inner padding and border in Firefox 3+.\r\n */\r\n\r\nbutton::-moz-focus-inner,\r\ninput::-moz-focus-inner {\r\n    border: 0;\r\n    padding: 0;\r\n}\r\n\r\n/**\r\n * 1. Remove default vertical scrollbar in IE 6/7/8/9.\r\n * 2. Improve readability and alignment in all browsers.\r\n */\r\n\r\ntextarea {\r\n    overflow: auto; /* 1 */\r\n    vertical-align: top; /* 2 */\r\n}\r\n\r\n/**\r\n * Remove most spacing between table cells.\r\n */\r\n\r\ntable {\r\n    border-collapse: collapse;\r\n    border-spacing: 0;\r\n}\r\n\r\nhtml,\r\nbutton,\r\ninput,\r\nselect,\r\ntextarea {\r\n    color: #222;\r\n}\r\n\r\n::-moz-selection {\r\n    background: #b3d4fc;\r\n    text-shadow: none;\r\n}\r\n\r\n::selection {\r\n    background: #b3d4fc;\r\n    text-shadow: none;\r\n}\r\n\r\nimg {\r\n    vertical-align: middle;\r\n}\r\n\r\nfieldset {\r\n    border: 0;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\ntextarea {\r\n    resize: vertical;\r\n}\r\n\r\n.chromeframe {\r\n    margin: 0.2em 0;\r\n    background: #ccc;\r\n    color: #000;\r\n    padding: 0.2em 0;\r\n}\r\n","@import 'fonts';\r\n@import 'reset';\r\n\r\n.container {\r\n    width: 100%;\r\n    height: 100vh;\r\n}\r\n\r\n.halfContainer {\r\n    width: 100%;\r\n    // min-height: 50vh;\r\n}\r\n\r\n.margin {\r\n    height: 80px;\r\n}\r\n\r\na {\r\n    text-decoration: none;\r\n    color: black;\r\n    &:hover {\r\n        cursor: pointer;\r\n        text-decoration: underline;\r\n        text-decoration-color: #f7af00;\r\n    }\r\n}\r\n\r\n@import 'home';\r\n@import 'about';\r\n@import 'portfolio';\r\n@import 'hireme';\r\n","@import 'fonts';\r\n\r\n@mixin font {\r\n    font-family: 'Cairo', sans-serif;\r\n}\r\n\r\n#home {\r\n    height: 100vh;\r\n    width: 100%;\r\n    display: flex;\r\n    .dropList {\r\n        display: none;\r\n    }\r\n    .navigation {\r\n        @include font;\r\n        font-siz